<template>
  <div class="user">
    <div class="photo" data-toggle="collapse" :aria-expanded="!isClosed" @click="toggleMenu">
      <img :src="sidebarLogo" alt="">
    </div>
    <div class="info">
      <a data-toggle="collapse" :aria-expanded="!isClosed" @click="toggleMenu" href="#" class="block-photo">
         <span>
           <b class="caret"></b>
        </span>
      </a>
      <a :href="host" target="_blank" title="Xem website" class="next-preview"><i class="fa fa-external-link"></i></a>
      <div class="clearfix"></div>
      <div>
        <collapse-transition>
          <ul class="nav nav-menu" style="padding-left: 44px;" v-show="!isClosed">
            <li>
              <a href="#" @click="user">
                <span class="sidebar-mini-icon"></span>
                <span class="sidebar-normal">Tài khoản của tôi</span>
              </a>
            </li>
            <li>
              <a href="#" @click="logout">
                <span class="sidebar-mini-icon"></span>
                <span class="sidebar-normal">Đăng xuất</span>
              </a>
            </li>
          </ul>
        </collapse-transition>
      </div>
    </div>
  </div>
</template>
<script>
  import { CollapseTransition } from 'vue2-transitions'
  import { Message } from 'element-ui';
  import { mapState } from 'vuex'

  export default {
    components: {
      CollapseTransition,
      Message
    },
    computed: {
      ...mapState(['sidebarLogo', 'sidebarTitle']),
      host(){
        return window.BASE_URL||'/';
      }
    },
    data() {
      return {
        isClosed: true
      }
    },
    methods: {
      toggleMenu() {
        this.isClosed = !this.isClosed
      },
      logout(){
        this.$store.dispatch('authLogout').then(res => {
          Message({
            message: 'Đăng xuất thành công',
            type: 'success'
          });
          this.$router.push({name: 'Login'})
        })
      },
      user(){
        let user = JSON.parse(this.$util.getCookie('user'));
        this.$router.push('/users/' + user.user_id);
      }
    }
  }
</script>
<style scoped>
  .nav.nav-menu {
    margin-top: 0;
  }
  .next-preview {
    position: absolute;
    font-size: 18px;
    padding: 0;
    top: 0px !important;
    right: 8px;
  }
  .user .photo, .user .block-photo{
    cursor: pointer;
    height: 40px;
  }

</style>
