import _ from 'lodash'
import store from '../store/store'
import sidebars from "../sidebarLinks";

let $root = {};

const nameHash = {
  'products': 'product',
  'product': 'product',

  'collections': 'collection',
  'collection': 'collection',

  'articles': 'article',
  'article': 'article',

  'blogs': 'blog',
  'blog': 'blog',

  'pages': 'page',
  'page': 'page',

  'galleries': 'gallery',
  'gallery': 'gallery',

  'customers': 'customer',
  'customer': 'customer',

  'roles': 'role',
  'role': 'role',

  'coupons': 'coupons',
  'coupon': 'coupon',
}

const modelNameHash = {
  'product': 'sản phẩm',
  'collection': 'nhóm sản phẩm',
  'article': 'bài viết',
  'blog': 'nhóm bài viết',
  'page': 'trang nội dung',
  'gallery': 'gallery',
  'customer': 'khách hàng',
  'role': 'quyền',
  'coupon': 'mã giảm giá'
}



const multipleTypes = [
  'products', 'collections', 'articles', 'blogs', 'pages', 'galleries', 'roles', 'customers', 'coupons'
]

const util = {
  findModel(_name) {
    let name = _name.toLowerCase();
    return nameHash[name];
  },
  findModelName(_model) {
    return modelNameHash[_model] || modelNameHash[util.findModel(_model)];
  },
  isMultiple(type) {
    return _.includes(multipleTypes, type);
  },
  formatMoney(value) {
      let val = (value/1).toFixed(0).replace('.', ',')
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  },
  formatVndShort(value) {
    let postfix = 'đ';
    if (value > 1e9) {
      value = Math.floor(value / 1e7) / 100;
      postfix = 'tỷ'
    } else if (value > 1e6) {
      value = Math.floor(value / 1e4) / 100;
      postfix = 'triệu'
    } else {
      value = (value/1).toFixed(0).replace('.', ',');
      value = value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    }

    return value + ' ' + postfix;
  },
  filterByHandle(fields, handleArr) {
    if (!handleArr) {
      return fields;
    }
    return fields.filter(f => handleArr.indexOf(f.handle) > -1);
  },
  timeSince(date) {
    var seconds = Math.floor((new Date() - new Date(date)) / 1000);
    var interval = Math.floor(seconds / 31536000);
    if (interval >= 1) {
      return interval + " năm trước";
    }
    interval = Math.floor(seconds / 2592000);
    if (interval >= 1) {
      return interval + " tháng trước";
    }
    interval = Math.floor(seconds / 86400);
    if (interval >= 1) {
      return interval + " ngày trước";
    }
    interval = Math.floor(seconds / 3600);
    if (interval >= 1) {
      return interval + " giờ trước";
    }
    interval = Math.floor(seconds / 60);
    if (interval >= 1) {
      return interval + " phút trước";
    }
    return Math.floor(seconds) + " giây trước";
  },
  nestedSet(hash, field, value) {
    let levels = field.split('.');
    let obj = hash;
    let lastLevel = levels.pop();
    for (let level of levels) {
      if (!obj[level]) {
        obj[level] = {};
      }
      obj = obj[level];
    }
    obj[lastLevel] = value;
  },
  nestedGet(hash, field) {
    let levels = field.split('.');
    let obj = hash;
    let lastLevel = levels.pop();
    for (let level of levels) {
      if (!obj[level]) {
        obj[level] = {};
      }
      obj = obj[level];
    }
    return obj[lastLevel];
  },

  hashFlatten(hash) {
    // {seo: {meta_title: ''}} => seo.meta_title
    let result = {};

    function setValue(hash, result, parent) {
      for (let key of Object.keys(hash)) {
        if (typeof hash[key] != 'object') {
          result[parent + key] = hash[key];
        } else {
          setValue(hash[key], result, parent + key + '.')
        }
      }
      return result;
    }

    return setValue(hash, result, '');
  },
  hashNested(hash) {
    let result = {};
    // seo.meta_title => {seo: {meta_title: ''}}
    for (let key of Object.keys(hash)) {
      util.nestedSet(result, key, hash[key]);
    }
    return result;
  },
  createHandle(str) {
    if (str) {
      str = str + '';
      str = str.trim();
      str = str.toLowerCase();
      str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
      str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
      str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
      str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
      str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
      str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
      str = str.replace(/đ/g, "d");
      str = str.replace(/\,/g, '-');
      str = str.replace(/\./g, '-');
      str = str.replace(/\!/g, '-');
      str = str.replace(/\?/g, '-');
      str = str.replace(/\~/g, '-');
      str = str.replace(/\ /g, '-');
      str = str.replace(/\|/g, '-');
      str = str.replace(/\./g, '-');
      str = str.replace(/\"/g, '-');
      str = str.replace(/\'/g, '-');
      str = str.replace(/\-\-+/g, '-');
      str = str.replace(/\s+/g, '-');
      str = str.replace(/[^\w\-]+/g, '');
      str = str.replace(/\-\-+/g, '-');
      str = str.replace(/^-+/, '');
      str = str.replace(/-+$/, '');
      if (str.slice(-1) == '-') str = str.substring(0, str.length - 1);
    }
    return str;
  },
  formatDataChartDashboard(data) {
    let days = 29;
    let hash = {};
    data.forEach(function (elem, i) {
      hash[elem.date] = elem.sum;
    });
    let labels = [];
    let result = [];
    for (let i = days; i >= 0; i--) {
      let date = new Date(new Date() - i * 86400000);
      let dateName = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + '-' + ((date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1));
      labels.push(dateName);
      if (hash[dateName]) {
        result.push(hash[dateName]);
      } else {
        result.push(0);
      }
    }
    let obj = {};
    obj.labels = labels;
    obj.result = result;
    return obj;
  },
  setCookie(name,value,days) {
    var expires = "";
    if (days) {
      var date = new Date();
      date.setTime(date.getTime() + (days*24*60*60*1000));
      expires = "; expires=" + date.toUTCString();
    }
    document.cookie = name + "=" + (value || "")  + expires + "; path=/";
  },
  getCookie(name) {
    var nameEQ = name + "=";
    var ca = document.cookie.split(';');
    for(var i=0;i < ca.length;i++) {
      var c = ca[i];
      while (c.charAt(0)==' ') c = c.substring(1,c.length);
      if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length,c.length);
    }
    return null;
  },
  eraseCookie(name) {
    this.setCookie(name, '', 0);
  },
  updateStatusAll(model, rows, status){
    let arrID = [];
    for (let row of rows) {
      arrID.push(row.id);
    }

    let data = {
      type: model,
      arrId: arrID,
      status: status
    }
    return store.dispatch('updateStatus', data)
  },
  checkRole(role){
    let self = this;
    let roles = JSON.parse(self.getCookie('role'));
    let roleID = util.getCookie('role_id');
    if (roleID == -1 || !roleID)  return true;
    else if (roles.indexOf(role) != -1) return true;
    else return false;
  },
  checkChangePassword(userID){
    let self = this;
    let user = JSON.parse(self.getCookie('user'));
    if (user.user_id == userID) return true;
    return false;
  },
  parseSelect(data){
    let arr = {};
    arr.options = [];
    for (let item of data) {
      arr.options.push({
        value: item.id + '',
        title: item.title
      });
    }
    return arr;
  },
  parseDataCustomfield(customfield, data, type, typeID){
    let arr_custom_field = [];
    for(let item of customfield) {
      let obj = {};
      let value = data[item.handle];
      if (item.input_type == 'maps' || item.input_type == 'images' || item.input_type == 'select-multiple') value = JSON.stringify(data[item.handle]);
      obj.title = item.title || item.name;
      obj.handle = item.handle;
      obj.post_type = type;
      obj.post_id = typeID;
      obj.value = value||'';
      arr_custom_field.push(obj);
    }
    return arr_custom_field;
  },
  getContext() {
    let data = {};
    for (let i = 0; i < arguments.length; i++) {
      data = Object.assign(data, arguments[i]);
    }
    return data;
  },
  getImageSize(attr) {
    let ctx = store.state.ctx;
    if (window.__FUNC.ImageSize) {
      let size = window.__FUNC.ImageSize(attr, ctx);
      return size === undefined ? '' : '(' + size + ')';
    }

    return '';
  },
  checkVisitDashboard(key) {
    if (window.__FUNC.Dashboard) {
      let arrField = window.__FUNC.Dashboard;
      let check = arrField.indexOf(key) == -1;
      return check;
    }

    return true;
  },
  changeFavicon(src){
    document.head || (document.head = document.getElementsByTagName('head')[0]);
    var link = document.createElement('link');
    var oldLink = document.getElementById('dynamic-favicon');
    link.id = 'dynamic-favicon';
    link.rel = 'shortcut icon';
    link.href = src;
    if (oldLink){
      document.head.removeChild(oldLink);
    }
    document.head.appendChild(link);
  }
}

setTimeout(_ => {
  $root = window.vueApp.$root;
}, 500);


export default util;
