import util from '../helpers/util'
import {router} from "../main.js"

function buildQuery(params) {
  if (!params) {
    return '';
  }
  if (typeof params === 'string') {
    return params;
  }
  if (typeof params !== 'object') {
    return '';
  }

  let arr = [];
  for (let key of Object.keys(params)) {
    arr.push(encodeURIComponent(key) + '=' + encodeURIComponent(params[key]));
  }
  return '?' + arr.join('&');
}

function mergeViewSettings(state, settings) {
  settings = settings.reduce(function (result, item) {
    if ((typeof item.key) != 'number') result[item.key] = item.value;
    return result;
  }, {});
  return settings;
}

