  [types.FETCH_SETTING_VIEW](state, settings) {
    state.settingsView = settings;
    localStorage.setItem('settingsView', JSON.stringify(settings));
  },

  [types.FETCH_VERSION_SETTING_VIEW](state, data) {
    let result = {
      multiple: false,
      options: []
    };
    data.version.forEach(function(element, index) {
      if (element) {
        if (!index) {
          result.options.push({
            title: 'Phiên bản hiện tại',
            value: element,
          })
        } else {
          let fileName = element.replace('admin_', '');
          fileName = fileName.replace('.php', '');
          let temp = fileName.split('_');
          fileName = temp[0] + '/' + temp[1] + '/' + temp[2] + ' ' + temp[3] + ':' + temp[4] + ':' + temp[5];
          result.options.push({
            title: fileName,
            value: element,
          })
        }
      }
    });
    state.versionSetting = result;
  },

